import React, { useState } from "react";
import styles from "../styles/style";
import { Container, Menu, Visibility, Icon } from "semantic-ui-react";

const Menus = () => {
  const [menuFixed, setMenuFixed] = useState(false)

  const [activeItem, setActiveItem] = useState('home')

  const stickTopMenu = () => setMenuFixed(true);

  const unStickTopMenu = () => setActiveItem(false);

  const handleItemClick = (e, { name }) => setActiveItem(name);

  return (
    <Visibility
      onBottomPassed={stickTopMenu}
      onBottomVisible={unStickTopMenu}
      once={false}
    >
      <Menu
        color="pink"
        pointing
        secondary
        borderless
        fixed={menuFixed ? "top" : undefined}
        style={menuFixed ? styles.fixedMenuStyle : styles.menuStyle}
      >
        <Container text textAlign="center">
          <Menu.Item
            link
            header
            href="#home"
            name="home"
            active={activeItem === "home"}
            onClick={handleItemClick}
          >
            <Icon name="user circle" size="large" /> &nbsp;&nbsp;Me
          </Menu.Item>

          <Menu.Item
            link
            header
            href="#me"
            name="me"
            active={activeItem === "me"}
            onClick={handleItemClick}
          >
            <Icon name="question circle" size="large" /> &nbsp;&nbsp;Who am I?
          </Menu.Item>

          <Menu.Item
            header
            link
            href="#experiences"
            name="experience"
            active={activeItem === "experience"}
            onClick={handleItemClick}
          >
            <Icon name="address book" size="large" />
            &nbsp;&nbsp;Experiences
          </Menu.Item>

          <Menu.Item
            header
            link
            href="#skillset"
            name="skillset"
            active={activeItem === "skillset"}
            onClick={handleItemClick}
          >
            <Icon name="wrench" size="large" />
            &nbsp;&nbsp;Skillset
          </Menu.Item>

          <Menu.Item
            header
            as="a"
            href="#contact"
            name="contact"
            active={activeItem === "contact"}
            onClick={handleItemClick}
          >
            <Icon name="object ungroup outline" size="large" />{" "}
            &nbsp;&nbsp;Contact
          </Menu.Item>
        </Container>
      </Menu>
    </Visibility>
  );
}
export default Menus


